import * as React from "react"
import Link from "next/link"
import { Menu, X } from "lucide-react"

import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"

export interface NavbarProps extends React.HTMLAttributes<HTMLElement> {
    logo?: React.ReactNode
    menuItems?: { label: string; href: string }[]
    action?: React.ReactNode
}

export function Navbar({
    className,
    logo = <span className="font-bold text-xl tracking-tight">Zero UI</span>,
    menuItems = [
        { label: "Features", href: "#" },
        { label: "Pricing", href: "#" },
        { label: "About", href: "#" },
    ],
    action = (
        <div className="flex gap-2">
            <Button variant="ghost" size="sm">Sign In</Button>
            <Button size="sm">Get Started</Button>
        </div>
    ),
    ...props
}: NavbarProps) {
    const [isOpen, setIsOpen] = React.useState(false)

    return (
        <nav
            className={cn(
                "fixed top-0 left-0 right-0 z-50 glass transition-all duration-300",
                className
            )}
            {...props}
        >
            <div className="container mx-auto px-4 h-16 flex items-center justify-between">
                {/* Logo */}
                <div className="flex items-center gap-2">
                    {logo}
                </div>

                {/* Desktop Menu */}
                <div className="hidden md:flex items-center gap-8">
                    {menuItems.map((item) => (
                        <Link
                            key={item.label}
                            href={item.href}
                            className="text-sm font-medium text-muted-foreground hover:text-primary transition-colors"
                        >
                            {item.label}
                        </Link>
                    ))}
                </div>

                {/* Desktop Actions */}
                <div className="hidden md:flex items-center gap-4">
                    {action}
                </div>

                {/* Mobile Toggle */}
                <div className="md:hidden">
                    <Button variant="ghost" size="icon" onClick={() => setIsOpen(!isOpen)}>
                        {isOpen ? <X className="h-5 w-5" /> : <Menu className="h-5 w-5" />}
                    </Button>
                </div>
            </div>

            {/* Mobile Menu */}
            {isOpen && (
                <div className="md:hidden border-t border-border bg-background/95 backdrop-blur-sm animate-accordion-down">
                    <div className="container px-4 py-4 flex flex-col gap-4">
                        {menuItems.map((item) => (
                            <Link
                                key={item.label}
                                href={item.href}
                                className="text-sm font-medium text-muted-foreground hover:text-primary transition-colors py-2"
                                onClick={() => setIsOpen(false)}
                            >
                                {item.label}
                            </Link>
                        ))}
                        <div className="pt-2 flex flex-col gap-2">
                            {/* Simplified mobile actions for demo */}
                            <Button variant="outline" className="w-full justify-center">Sign In</Button>
                            <Button className="w-full justify-center">Get Started</Button>
                        </div>
                    </div>
                </div>
            )}
        </nav>
    )
}
